'use server';
/**
 * @fileOverview A legal research assistant AI agent.
 *
 * - legalResearchAssistant - A function that handles legal research queries.
 * - LegalResearchInput - The input type for the legalResearchAssistant function.
 * - LegalResearchOutput - The return type for the legalResearchAssistant function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const CaseLawSchema = z.object({
  caseName: z.string().describe('The name of the court case.'),
  citation: z.string().describe('The legal citation for the case.'),
  summary: z.string().describe('A brief summary of the case relevant to the research topic.'),
});

const StatuteSchema = z.object({
  statuteName: z.string().describe('The name or number of the statute.'),
  citation: z.string().describe('The legal citation for the statute.'),
  summary: z.string().describe('A brief summary of the statute relevant to the research topic.'),
});

const LegalResearchInputSchema = z.object({
  researchTopic: z
    .string()
    .describe('The specific legal topic or question to research.'),
  caseContext: z
    .string()
    .optional()
    .describe(
      'Optional context about a specific case to tailor the research. This could include case facts, party names, or current procedural posture.'
    ),
});
export type LegalResearchInput = z.infer<typeof LegalResearchInputSchema>;

const LegalResearchOutputSchema = z.object({
  relevantCaseLaw: z
    .array(CaseLawSchema)
    .describe('A list of relevant case law, including citations and summaries.'),
  relevantStatutes: z
    .array(StatuteSchema)
    .describe('A list of relevant statutes, including citations and summaries.'),
  legalAnalysis: z
    .string()
    .describe(
      'A concise legal analysis based on the research, addressing the input topic and context.'
    ),
});
export type LegalResearchOutput = z.infer<typeof LegalResearchOutputSchema>;

export async function legalResearchAssistant(
  input: LegalResearchInput
): Promise<LegalResearchOutput> {
  return legalResearchAssistantFlow(input);
}

const prompt = ai.definePrompt({
  name: 'legalResearchAssistantPrompt',
  input: {schema: LegalResearchInputSchema},
  output: {schema: LegalResearchOutputSchema},
  prompt: `You are an expert legal research assistant. Your task is to provide comprehensive legal research based on the user's query.

Research Topic: {{{researchTopic}}}

{{#if caseContext}}
Case Context: {{{caseContext}}}
Please tailor your research to be relevant to this specific case context.
{{/if}}

Identify and provide:
1.  Relevant Case Law: Include the case name, full citation, and a brief summary of its relevance to the research topic.
2.  Relevant Statutes: Include the statute name/number, full citation, and a brief summary of its relevance.
3.  Legal Analysis: Provide a concise analysis synthesizing the findings from case law and statutes, directly addressing the research topic and incorporating the case context if provided.

Focus on accuracy and relevance. Ensure citations are in a standard legal format.
`,
});

const legalResearchAssistantFlow = ai.defineFlow(
  {
    name: 'legalResearchAssistantFlow',
    inputSchema: LegalResearchInputSchema,
    outputSchema: LegalResearchOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    if (!output) {
      throw new Error('No output from AI model for legal research.');
    }
    return output;
  }
);
